.header {
  max-width: 880px;
  width: 100%;
  padding: 45px 0 36px;
  margin: 0 auto;
  border-bottom: 1px solid #545454;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 1024px) {
  .header {
    width: auto;
    padding-top: 28px;
    padding-left: 27px;
    padding-bottom: 26px;
  }
}
